.mainDiv {
  height: 100%;
  width: 100%;
}

.mainLotteryDiv {
  top: 25px;
  position: relative;
}

.headlineTitle {
  display: flex; /* center the rows horizontally */
  align-items: center; /* center the rows horizontally */
  justify-content: center; /* center the rows horizontally */
  flex-direction: row; /* center the rows horizontally */
  font-size: 25px;
  font-weight: 800;
  margin-right: 1em;
}

.lotteryRows {
  display: flex; /* center the table horizontally */
  align-items: center; /* center the table horizontally */
  justify-content: center; /* center the table horizontally */
  flex-direction: column; /* center the table horizontally */
}

.lotteryTitleStyle {
  margin-left: 0.3em;
}

.rowsNumber {
  color: #223977;
  margin-left: 0.5em;
  width: 5.5em;
}

.lotteryButtons {
  display: flex; /* center the div horizontally */
  align-items: center; /* center the div horizontally */
  justify-content: center; /* center the div horizontally */
  flex-direction: row; /* center the div horizontally */
}

.lotteryButtons button {
  background-color: #223977;
  color: #fff;
  border-radius: 6px;
  font-weight: 700;
  letter-spacing: 1px;
  padding-left: 8px;
  padding-right: 8px;
  font-size: 1.2em;
  margin-left: 15px;
}

.lotteryButtons button:hover {
  background-color: rgba(34, 57, 119, 0.7);
}

@media only screen and (max-width: 800px) {
  .lotteryButtons button:hover {
    background-color: #223977;
  }
}

td {
  background-color: #ececec;
}

.lotteryRows table {
  padding-top: 20px;
  font-size: 1em;
  font-weight: 800;
}

.lotteryRows td {
  text-align: center;
  vertical-align: middle;
}

.tdRowNumber {
  width: 2em;
}

.tdStrongNumber {
  width: 6em;
}

.lotteryRows .lotteryNumbersCircle {
  margin-left: 4px;
  color: #fff;
  border-radius: 50%;
  width: 1.8em;
  height: 1.8em;
  display: inline-block;
  line-height: 1.8em;
  text-align: center;
}